.canvas {
  width: 100vw;
  height: 80vh;
}

header {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
}

.clip-button {
  position: absolute;
  border: none;
  color: white;
  cursor: pointer;
  font-size: 1em;
  transition: background-color 0.3s;
  -webkit-clip-path: inherit;
  clip-path: inherit;
  width: 100%;
  height: 100%;
}

